.page-break {
  page-break-after: always;
}

.page-before {
  page-break-before: always;
}

.main-color {
  color: #EC2028;
}

.main-color-dark {
  color: #333;
}

.main-bg {
  background-color: #EC2028;
}

.main-bg-dark {
  background-color: #333;
}

@page {
  header: page-header;
  footer: page-footer;
  margin: 1cm;
}

body {
  font-family: "XB Riyaz", sans-serif;
  font-size: 13px;
}

.separator-margin-10 {
  margin-top: 10px;
  margin-bottom: 10px;
}

.separator-margin-20 {
  margin-top: 20px;
  margin-bottom: 20px;
}

.separator-margin-30 {
  margin-top: 30px;
  margin-bottom: 30px;
}

.separator-margin-40 {
  margin-top: 40px;
  margin-bottom: 40px;
}

.separator-margin-50 {
  margin-top: 50px;
  margin-bottom: 50px;
}

.separator-margin-60 {
  margin-top: 60px;
  margin-bottom: 60px;
}

.separator-margin-70 {
  margin-top: 70px;
  margin-bottom: 70px;
}

.separator-margin-80 {
  margin-top: 80px;
  margin-bottom: 80px;
}

.separator-margin-90 {
  margin-top: 90px;
  margin-bottom: 90px;
}

.separator-margin-100 {
  margin-top: 100px;
  margin-bottom: 100px;
}

.border-center {
  border-top: 1px solid #BBB;
}

.clear {
  clear: both;
  display: block;
  overflow: hidden;
}

.f-left {
  float: left !important;
}

.f-right {
  float: right !important;
}

.ltr {
  direction: ltr !important;
}

.rtl {
  direction: rtl !important;
}

.line {
  line-height: 1.5;
}

.line b {
  color: #224161;
}

.line .line-number {
  color: #224161;
  font-weight: bold;
}

.bold {
  font-weight: bold;
}

.mark-red {
  color: #cf1938 !important;
}

.text-center {
  text-align: center !important;
}

.title-center {
  font-size: 17px !important;
  margin-bottom: 5px;
}

.page-header {
  height: 60px;
}

.page-header .pdf-title {
  width: 60%;
  float: right;
}

.page-header .pdf-title .title {
  font-size: 22px;
  line-height: 50px;
  margin: 0;
  display: inline-block;
  color: #333;
  border: 1px solid #CCC;
  padding: 0 10px;
  border-radius: 2px;
  font-weight: bold;
}

.page-header .pdf-title .line a {
  color: #444;
  text-decoration: none;
}

.page-header .logo {
  width: 40%;
  float: left;
}

body {
  direction: rtl;
  text-align: right;
}

.page-header {
  height: 50px;
}

.page-header .pdf-title {
  float: right;
  text-align: left;
  color: #000;
  width: 110px;
}

.page-header .pdf-title .line {
  font-size: 11px;
}

.wrapper-content .content .page-title {
  margin-top: 10px;
  margin-bottom: 0;
  font-size: 20px;
  color: #EC2028;
}

.wrapper-content .content .page-title .border {
  width: 190px;
  margin: 3px auto;
  height: 2px;
  background-color: #333;
}

.wrapper-content .content .receipt-id {
  text-align: left;
  font-size: 20px;
  font-weight: bold;
}

.wrapper-content .content .receipt-lines .line {
  color: #333;
  font-size: 14px;
  margin-bottom: 5px;
  padding: 5px;
  padding-bottom: 10px;
  font-weight: bold;
}

.wrapper-content .content .receipt-lines .line.border {
  border-bottom: 1px solid #CCC;
}

.wrapper-content .content .receipt-lines .line .title {
  display: inline-block;
  color: #EC2028;
  padding-left: 5px;
}

.wrapper-content .content .signatures {
  border-top: 1px solid #333;
  padding-top: 15px;
}

.wrapper-content .content .signatures .signature {
  width: 50%;
  text-align: center;
  float: right;
}

